"use client"
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { useState, useEffect } from "react";
import { toast } from "sonner";
import { useRouter } from "next/navigation";

// 表单验证模式
const formSchema = z.object({
  username: z.string()
    .min(3, { message: "Username must be at least 3 characters" })
    .max(20, { message: "Username must not exceed 20 characters" }),
  password: z.string()
    .min(6, { message: "Password must be at least 6 characters" }),
  confirmPassword: z.string(),
  captcha: z.string()
    .min(1, { message: "Captcha is required" })
}).refine((data) => data.password === data.confirmPassword, {
  message: "Passwords do not match",
  path: ["confirmPassword"],
});

type RegisterFormValue = z.infer<typeof formSchema>;

export default function RegisterForm() {
  const router = useRouter();
  const [captcha, setCaptcha] = useState<{ captchaId: string; captchaUrl: string } | null>(null);

  const form = useForm<RegisterFormValue>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      username: "",
      password: "",
      confirmPassword: "",
      captcha: "",
    },
  });

  // 获取验证码
  const getCaptcha = async () => {
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/captcha/new`);
      const data = await response.json();
      setCaptcha({
        captchaId: data.captcha_id,
        captchaUrl: `${process.env.NEXT_PUBLIC_API_URL}/captcha/${data.captcha_id}?t=${new Date().getTime()}`
      });
    } catch (error) {
      console.error('Failed to get captcha:', error);
      toast.error('Failed to load captcha');
    }
  };

  // 注册提交
  const onSubmit = async (data: RegisterFormValue) => {
    try {
      if (!captcha?.captchaId) {
        throw new Error('Captcha not loaded');
      }

      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'captchaId': captcha.captchaId,
          'userInput': data.captcha
        },
        body: JSON.stringify({
          username: data.username,
          password: data.password
        })
      });

      const result = await response.json();

      if (!response.ok) {
        const errorMessage = result.error || result.message || result.msg || 'Unknown error';
        if (errorMessage.includes('already exists')) {
          toast.error('Registration failed: Username already exists');
        } else if (errorMessage.includes('captcha')) {
          toast.error('Registration failed: Invalid captcha');
        } else {
          toast.error(`Registration failed: ${errorMessage}`);
        }
        getCaptcha();
        return;
      }

      toast.success('Registration successful!');
      router.push('/login');

    } catch (error) {
      console.error('Registration error:', error);
      toast.error('Registration failed: Please try again');
      getCaptcha();
    }
  };

  // 初始化加载验证码
  useEffect(() => {
    getCaptcha();
  }, []);

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
        <FormField
          control={form.control}
          name="username"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Username</FormLabel>
              <FormControl>
                <Input {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="password"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Password</FormLabel>
              <FormControl>
                <Input type="password" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="confirmPassword"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Confirm Password</FormLabel>
              <FormControl>
                <Input type="password" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <div className="flex gap-4 items-end">
          <FormField
            control={form.control}
            name="captcha"
            render={({ field }) => (
              <FormItem className="flex-1">
                <FormLabel>Captcha</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          {captcha?.captchaUrl && (
            <img
              src={captcha.captchaUrl}
              alt="captcha"
              className="h-10 cursor-pointer"
              onClick={() => getCaptcha()}
            />
          )}
        </div>

        <Button type="submit" className="w-full">
          Register
        </Button>
      </form>
    </Form>
  );
}